import Table from 'react-bootstrap/Table';
import React from 'react';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import { render } from '@testing-library/react';
import { useState } from 'react';
import {Link, NavLink} from 'react-router-dom';
import {useNavigate} from 'react-router-dom';
import Home from './App';
import {useHistory} from 'react-router-dom'
import axios from 'axios';
import { Redirect } from "react-router-dom"


function Write(){
    var [title, setTitle] = useState("");
    var [writer, setWriter] = useState("");
    var [cont, setCont] = useState("");
        
    
    

    return(
    <div style={{textAlign:"right", marginRight:"50px"}}>
    <Form className="Form" onSubmit={()=>Write01(title, writer, cont)}>
      <Form.Control id="title" type="text" placeholder="제목을 입력하세요" value={title}
      onChange={e=>setTitle(e.target.value)}/>
      <Form.Control id="writer" type="text" placeholder="작성자를 입력하세요" value={writer}
      onChange={e=>setWriter(e.target.value)}/>
      <Form.Control id="cont" as="textarea" rows={15} placeholder="내용을 입력하세요" value={cont}
      onChange={e=>setCont(e.target.value)}/>
      <Button variant="primary" type="button" onClick={()=>Write01(title, writer, cont)}>
          글쓰기
      </Button>    
    </Form>
    </div>
    );
  }


