import { useParams } from 'react-router-dom';


function Edit(){
    var [title, setTitle] = useState("");
    var [writer, setWriter] = useState("");
    var [cont, setCont] = useState("");
        
    var [title1, setTitle1] = useState("");
    var [writer1, setWriter1] = useState("");
    var [cont1, setCont1] = useState("");
    var [bno, setbno] = useState();

    const params = useParams();
    
    
    useEffect(() => {
        setTitle1(params.title);
    setWriter1(params.writer);
    setCont1(params.cont);
    setbno(params.bno);
        }, []);
    
    //alert("bno:"+bno);


    return(
    <div style={{textAlign:"right", marginRight:"50px"}}>
    <Form className="Form">
      <Form.Control id="title" type="text" placeholder={title1} value={title}
      onChange={e=>setTitle(e.target.value)}/>
      <Form.Control id="writer" type="text" placeholder={writer1} value={writer}
      onChange={e=>setWriter(e.target.value)}/>
      <Form.Control id="cont" as="textarea" rows={15} placeholder={cont1} value={cont}
      onChange={e=>setCont(e.target.value)}/>
      <Button variant="primary" type="button" onClick={()=>Edit01(bno, title, writer, cont)}>
          수정완료
      </Button>    
    </Form>
    </div>
    );
  }


